@import url(https://fonts.googleapis.com/css?family=Open+Sans|Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cuprum&display=swap);
@import url(https://fonts.googleapis.com/css?family=Saira+Condensed:600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Asap&display=swap);
* {
  box-sizing: border-box;
}

html {
  background-image: url(/portfolio/static/media/orange-cloud.46996aca.jpg);
  position: fixed;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
}

.mobile-menu-btn {
  display: none;
}

.box {
  background-color: #a78682;
  background-size: cover;
  position: absolute;
  width: 1000px;
  height: 600px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0 auto;
  box-shadow: 0px 0px 100px 0px #3f3f3f;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: 1fr 8fr;
}

.content {
  /* border: solid; */
  grid-column: 1 / 8;
  grid-row: 2 / 3;
  display: grid;
  grid-template-columns: 55% 45%;
  /* grid-template-rows: auto; */
  background-color: #efd9ce;
  z-index: -1;
}
/* TODO: FIX CONTENT MOVING UP */
.computer {
  box-shadow: inset -35px 55px 15px -20px #efd9ce;
  float: left;
  grid-column: 1 / 2;
  grid-row: 1 / 6;
}

.computer img {
  max-width: 100%;
  z-index: -1;
  position: relative;
  display: block;
  /* top: 40px; */
}

h1.name {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  color: #25283d;
  /* border: solid; */
  padding: 0px 0px 0px 20px;
  font-family: "Open Sans";
}

p.about {
  grid-column: 2 / 3;
  grid-row: 3 / 4;
  color: #25283d;
  /* border: solid; */
  padding: 30px 50px 30px 20px;
  font-family: "Raleway";
  /* padding-right: 50px; */
}

.icons {
  grid-column: 2 / 3;
  grid-row: 4 / 5;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.icons button:hover {
  opacity: 1;
  cursor: pointer;
}

a.resume {
  grid-column: 1 / 2;
  margin: 0 auto;
}
button.resume {
  font-size: 30px;
  border: solid;
  border-radius: 100px;
  padding: 10px 15px 10px 15px;
  background-color: transparent;
  color: #25283d;
  opacity: 0.5;
}

a.linkedin {
  grid-column: 2 / 3;
  margin: 0 auto;
}

button.linkedin {
  font-size: 30px;
  border: solid;
  border-radius: 100px;
  padding: 10px 15px 10px 15px;
  background-color: #e9e2d0;
  color: rgb(49, 119, 176);
  border-color: rgb(49, 119, 176);
  opacity: 0.5;
}

a:focus,
button:focus {
  outline: none;
}

a.github {
  grid-column: 3 / 4;
  margin: 0 auto;
}
button.github {
  font-size: 30px;
  border: solid;
  border-radius: 100px;
  padding: 10px 15px 10px 15px;
  background-color: #e9e2d0;
  color: black;
  opacity: 0.5;
}

/********** LANGUAGES **********/

.languages {
  grid-column: 1 / 8;
  grid-row: 2 / 6;
  display: grid;
  grid-template-columns: 5% 90% 5%;
  grid-template-rows: 20% 70% 10%;
  width: 100%;
  background-color: #efd9ce;
}

.languages-title {
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  /* background-color: rgb(214, 209, 216); */
  border-radius: 10px;
  margin: 0 auto;
  display: flex;
  /* padding: 10px 10px 10px 10px; */
  margin-left: 10px;
  margin-top: 30px;
  margin-bottom: 30px;
  box-shadow: inset 400px 0 0 0 rgb(214, 209, 216);
  /* -webkit-box-shadow: 0px 0px 12px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 12px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 12px -5px rgba(0, 0, 0, 0.75); */
}

.languages-title button {
  border: none;
  font-weight: bold;
  color: #a78682;
  font-size: 20px;
  font-family: "Open Sans";
  padding: 10px;
  border-radius: 10px;
  transition: all 0.25s 0.1s ease-out;
  -webkit-transition: all 0.25s 0.1s ease-out;
  -moz-transition: all 0.25s 0.1s ease-out;
  box-shadow: inset 400px 0 0 0 rgb(213, 209, 216);
  cursor: pointer;
}

button.active-languages-option {
  box-shadow: inset 400px 0 0 0 white;
}

.frameworks-title {
  grid-column: 7 / 8;
}

.panel-container {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  /* height: 100%; */
  display: flex;
  box-shadow: 0px 0px 46px -14px rgba(0, 0, 0, 0.75);
}

.open {
  /* border: solid; */
  /* border-color: white; */
  height: 100%;
  width: 170%;
  overflow: hidden;
  align-items: center;
  display: flex;
  text-align: center;
  background-color: black;
  -webkit-transition: all 0.25s 0.1s ease-out;
  transition: all 0.25s 0.1s ease-out;
}

.open img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.close {
  height: 100%;
  width: 20%;
  overflow: hidden;
  align-items: center;
  opacity: 0.7;
  background-color: black;
  text-align: center;
  -webkit-transition: all 0.25s 0.1s ease-out;
  transition: all 0.25s 0.1s ease-out;
}

.close:hover {
  opacity: 1;
}

.close img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.open img {
  border-color: white;
}

/********** PROJECTS **********/

.projects {
  grid-column: 1 / 8;
  grid-row: 2 / 6;
  background-color: #efd9ce;
  display: grid;
  grid-template-columns: 5% 90% 5%;
  grid-template-rows: 5% 10% 82% 3%;
}
.projects-menu {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  border-radius: 10px;
  margin: 0 auto;
  padding-bottom: 47px;
  /* display: flex; */
  /* padding: 10px 10px 10px 10px; */
  margin-left: 10px;
  /* margin-top: 30px; */
  margin-bottom: 10px;
  box-shadow: inset 400px 0 0 0 rgb(214, 209, 216);
}
.projects-menu button {
  border: none;
  font-weight: bold;
  color: #a78682;
  font-size: 20px;
  font-family: "Open Sans";
  padding: 10px;
  border-radius: 10px;
  transition: all 0.25s 0.1s ease-out;
  -webkit-transition: all 0.25s 0.1s ease-out;
  -moz-transition: all 0.25s 0.1s ease-out;
  box-shadow: inset 400px 0 0 0 rgb(213, 209, 216);
  cursor: pointer;
}

button.active-projects {
  box-shadow: inset 400px 0 0 0 white;
  color: #a78682;
}

.project-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
  padding-top: 10px;
  grid-column: 2 / 3;
  grid-row: 3 / 4;
  /* border: solid; */
  overflow-y: scroll;
}

.project-box {
  background-color: #25283d;
  border-radius: 10px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  color: #efd9ce;
}

.project-image {
  grid-column: 1 / 2;
  padding: 10px;
}
.project-image img {
  max-width: 100%;
  min-height: 100%;
  object-fit: cover;
  overflow-x: scroll;
}

.project-info {
  padding: 20px 20px 10px 20px;
}

.project-title {
  font-family: "Cuprum", sans-serif;
  font-size: 26px;
  padding-bottom: 10px;
}

.coming-soon {
  background-image: url(/portfolio/static/media/MiamiHacks.894bef2f.png);
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-position: center;
}

strong {
  font-size: 18px;
}
.contributions {
  grid-row: 2 / 3;
  font-size: 16px;
  grid-column: 1 / 5;
  padding: 0px;
  font-family: "Raleway";
  padding-top: 5px;
  padding-bottom: 5px;
}

.contributions ul,
.tech-stack ul {
  padding: 5px 0px 5px 0px;
  margin: 0 auto;
}

li {
  padding: 2px;
}

.tech-stack ul {
  list-style: none;
}

ul.stack {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.tech-stack {
  font-size: 16px;
  font-family: "Raleway";
  padding-top: 5px;
  padding-bottom: 5px;
}

.code-link {
  display: block;
  padding-top: 5px;
  padding-bottom: 5px;
}

.code-link button {
  font-family: "Raleway";
  font-weight: bolder;
  font-size: 14px;
  padding: 5px 10px 5px 10px;
  margin: 5px;
  border-radius: 5px;
  background-color: #a8aec1;
  border-color: #a8aec1;
  color: white;
  box-shadow: 0px 0px 10px 0px black;
  cursor: pointer;
}

.project-link {
  margin: auto;
  padding: 0px;
}

.project-link button:hover {
  background-color: rgb(154, 156, 179);
  border-color: rgb(154, 156, 179);
}

.private button:hover {
  background-color: #a8aec1;
  border-color: #a8aec1;
}

/********** NAVBAR **********/

.navigation {
  /* border: solid; */
  grid-column: 2 / 7;
  grid-row: 1 / 2;
  display: grid;
  padding: 0;
  grid-template-rows: 25% 75%;
}

.inner-nav {
  /* border: solid; */
  grid-row: 2 / 3;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* padding: 10px; */
  align-items: center;
  display: flex;
}

.inner-nav a {
  grid-row: 2 / 3;
  color: #25283d;
  margin: 0 auto;
  font-family: "Saira Condensed", sans-serif;
  font-size: 20px;
  padding: 15px;
  font-weight: 600;
  /* border: solid; */
}

/* .navigation li:hover {
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
} */

.inner-nav a:active,
.inner-nav a.active {
  background-color: #efd9ce;
}

a {
  text-decoration: none;
}

.active {
  background-color: #efd9ce;
}

/********** CONTACT **********/

.contact {
  background-color: #efd9ce;
  grid-column: 1 / 8;
  grid-row: 2 / 3;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(8, 1fr);
  /* grid-auto-rows: minmax(30px, 50px); */
  color: #25283d;
  font-family: "Open Sans";
}
/*
.contact div {
  max-height: 100%;
} */

.message {
  background-color: transparent;
  width: 100%;
  /* height: 100%; */
  color: #25283d;
  font-size: 16px;
  padding: 10px;
  border-radius: 5px;
}

.message::-webkit-input-placeholder {
  color: #25283d;
  opacity: 0.5;
}

.message:-ms-input-placeholder {
  color: #25283d;
  opacity: 0.5;
}

.message::-ms-input-placeholder {
  color: #25283d;
  opacity: 0.5;
}

.message::placeholder {
  color: #25283d;
  opacity: 0.5;
}

.message-name {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  /* display: inline; */
}

.message-email {
  grid-row: 2 / 3;
  grid-column: 4 / 5;
  /* display: flex; */
}

.message-content {
  grid-row: 4 / 8;
  grid-column: 2 / 5;
}

.message-content input {
  height: 100%;
}

@media only screen and (max-width: 414px) {
  html {
    background-color: white; /* Red */
    background-image: none;
  }
  .box {
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    grid-template-columns: 100%;
    grid-template-rows: 100%;
    background-color: #efd9ce;
  }

  /* MENU */
  .mobile-menu-btn {
    display: block;
    font-size: 30px;
    position: absolute;
    top: 10px;
    right: 50%;
    left: 50%;
    color: #25283d;
    border: none;
    background-color: transparent;
  }

  .mobile-menu {
    height: 100%;
    width: 100%;
    background-color: #25283d;
    display: block;
    position: absolute;
    font-size: 18px;
    height: 60%;
    font-family: "Asap", sans-serif;
    padding-top: 10px;
    padding-bottom: 30px;
    /* grid-template-rows: repeat(4, 1fr); */
    /* z-index: 1; */
  }

  .mobile-menu a {
    /* border: solid; */
    height: 25%;
    /* padding: 20px; */
    display: flex;
    justify-content: center;
    align-items: center;
    color: #efd9ce;
  }

  .exit-menu {
    position: absolute;
    right: 20px;
    top: 10px;
    color: #efd9ce;
    font-size: 20px;
    border: none;
    background-color: transparent;
  }

  .navigation {
    display: none;
  }

  /*++++++++ ABOUT ME ++++++++*/
  .content {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 50% 10% 10% 30%;
    grid-template-columns: 100%;
    background-color: #efd9ce;
  }
  .content div {
    /* border: solid; */
  }
  .computer {
    /* height: 100%; */
    /* width: 100%; */
    grid-row: 1 / 2;
    box-shadow: none;
  }

  h1.name {
    grid-row: 3 / 4;
    grid-column: 1 / 2;
    margin: auto;
    /* border: solid; */
    font-size: 24px;
    padding: 20px 20px 20px 10px;
    color: #25283d;
  }

  .icons {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
    margin: 0 auto;
    display: flex;
  }

  .icons a {
    padding: 10px;
  }
  button.resume {
    font-size: 30px;
    border: solid;
    border-radius: 100px;
    padding: 10px 15px 10px 15px;
    background-color: transparent;
    color: #51355a;
    opacity: 1;
  }
  button.linkedin {
    font-size: 30px;
    border: solid;
    border-radius: 100px;
    padding: 10px 15px 10px 15px;
    background-color: transparent;
    color: rgb(49, 119, 176);
    border-color: rgb(49, 119, 176);
    opacity: 1;
  }
  button.github {
    font-size: 30px;
    border: solid;
    border-radius: 100px;
    padding: 10px 15px 10px 15px;
    background-color: transparent;
    color: black;
    opacity: 1;
  }

  p.about {
    /* border: solid; */
    grid-row: 4 / 5;
    grid-column: 1 / 2;
    padding: 30px;
    margin: 0;
    color: #25283d;
  }
}

@media only screen and (max-width: 320px) {
  .content {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 40% 10% 15% 35%;
    grid-template-columns: 100%;
    background-color: #efd9ce;
  }
  h1.name {
    grid-row: 3 / 4;
    grid-column: 1 / 2;
    margin: auto;
    /* border: solid; */
    font-size: 24px;
    padding: 20px 20px 20px 30px;
    color: #25283d;
  }
}

/* IPHONE5/SE */
@media only screen and (max-height: 738px) {
  .content {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 55% 10% 10% 25%;
    grid-template-columns: 100%;
    background-color: #efd9ce;
  }
  h1.name {
    grid-row: 3 / 4;
    grid-column: 1 / 2;
    margin: auto;
    /* border: solid; */
    font-size: 20px;
    padding: 20px 20px 20px 10px;
    color: #25283d;
  }

  p.about {
    padding-bottom: 230px;
  }
  button.resume {
    font-size: 20px;
  }

  button.linkedin {
    font-size: 20px;
  }

  button.github {
    font-size: 20px;
  }
}

